import React from "react"
import { Link } from "gatsby"
import Img from "gatsby-image"

import Layout from "../components/layout"
import SEO from "../components/seo"

const Badge = ({
  id,
  title,
  score,
  percentile,
  dateCompleted,
  level,
  localImage,
}) => (
  <Link to={id}>
    <h1>{title}</h1>
    
    <div>{score}</div>
    <div>{Math.floor(percentile)}%</div>
    <div>{dateCompleted}</div>
    <div>{level}</div>
    <Img
      fluid={localImage.childImageSharp.fluid}
      alt={title}
      style={{ float: "left", marginRight: "1rem", width: 150 }}
    />
  </Link>
)

const SecondPage = ({ data }) => {
  const badges = data.allPluralsightJson.edges.map(({ node }) => (
    <Badge key={node.dateCompleted} {...node} />
  ))
  return (
    <Layout>
      <SEO title="Building My Blog" />
      <h1>Building My Blog</h1>
      <p>
        Trying out Gatsbyjs because I can. Here are some of my skill assessments
        from pluralsight
      </p>
      {badges}
      <Link to="/">Go back to the homepage</Link>
    </Layout>
  )
}

export default SecondPage

export const query = graphql`
  query {
    allPluralsightJson {
      edges {
        node {
          id
          percentile
          title
          dateCompleted(fromNow: false)
          level
          localImage {
            childImageSharp {
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
          score
        }
      }
    }
  }
`
